<template>
  <demo-article>
    <p></p>
    <example title="">
      <div slot="demo">
        <animate-text
          class="animate-text-string"
          :time="700"
          :value="string">
        </animate-text>
        <animate-text
          class="animate-text-number"
          :value="number"
          :is-number="true"
          :start-number="0"
          :time="2000"
          :change-count="50"
          >
        </animate-text>
      </div>
      <div slot="desc">
        <p></p>
        <p>, </p>
        <p>, </p>
      </div>
      <x-code lang="html" slot="code">{{ code.string }}</x-code>
      <x-code lang="html" slot="code">{{ code.number }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/animate-text'

export default {
  name: 'AnimateTexts',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      string: '<br> ,<br> ',
      number: 6666666.666
    }
  },

  mounted () {
    setTimeout(() => {
      this.string = '<br> ,<br> , <br> !<br>'
    }, 3000)
    setTimeout(() => {
      this.number = -12345.678
    }, 5000)
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'

.animate-text-string
  width 100%
  height 100px
.animate-text-number
  width 100%
  height 40px
  margin-top 20px
</style>



