let code = {}

code.drag = `
<draggable
  :data="'drop'"
  @dragstart="onSourceDragStart"
  @dragsend="onSourceDragEnd">

  <div class="drag-content"></div>

</draggable>
`

code.drop = `
<droppable
  class="drop-box"
  @dragstart="onDragStart"
  @dragend="onDragEnd"
  @drop="onDrop"
  @dragenter="onDragEnter"
  @dragover="onDragOver"
  @dragleave="onDragLeave">

  <div></div>

</droppable>
`

export default code



