<template>
  <demo-article>
    <p><code>XRow</code><code>XCol</code></p>
    <example title="">
      <div slot="demo">
        <x-row class="mb20">
          <x-col :span="24">
            <div class="content-container">24</div>
          </x-col>
        </x-row>
        <x-row class="mb20">
          <x-col :span="12">
            <div class="content-container">12</div>
          </x-col>
          <x-col :span="12">
            <div class="content-container">12</div>
          </x-col>
        </x-row>
        <x-row class="mb20">
          <x-col :span="8">
            <div class="content-container">8</div>
          </x-col>
          <x-col :span="8">
            <div class="content-container">8</div>
          </x-col>
          <x-col :span="8">
            <div class="content-container">8</div>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p><code>&lt;x-col&gt;</code>  <code>&lt;x-row&gt;</code> </p>
        <p>24 <code>x-col</code>  <code>span</code> </p>
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <x-row class="mb20" :gutter="10">
          <x-col :span="8">
            <div class="content-container">8</div>
          </x-col>
          <x-col :span="8">
            <div class="content-container">8</div>
          </x-col>
          <x-col :span="8">
            <div class="content-container">8</div>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p><code>&lt;x-row&gt;</code><code>gutter</code> </p>
      </div>
      <x-code lang="html" slot="code">{{ code.gutter }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <x-row class="mb20">
          <x-col :span="8" :offset="4">
            <div class="content-container">8</div>
          </x-col>
        </x-row>
        <x-row>
          <x-col :span="16" :push="8">
            <div class="content-container">16</div>
          </x-col>
          <x-col :span="8" :pull="16">
            <div class="content-container">8</div>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p><code>&lt;x-col&gt;</code><code>offset</code><code>push</code>  <code>pull</code> </p>
      </div>
      <x-code lang="html" slot="code">{{ code.offset }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/grid'

export default {
  name: 'GridDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/styles/common'

.example-demo
  [class^='x-col']
    .content-container
      background-color rgba(extra-light-gray, 30%)
      line-height 30px
      text-align center
      color light-gray

    &:nth-child(odd)
      .content-container
        background-color rgba(extra-light-gray, 50%)
</style>



