<template>
  <demo-article>
    <p>-</p>
    <example title="">
      <div slot="demo">
        <checkbox v-model="single"></checkbox>
        <p>{{ single }}</p>
      </div>
      <div slot="desc">
        <p><code>v-model</code></p>
      </div>
      <x-code lang="html" slot="code">{{ code.single }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <checkbox-group v-model="os" @change="onOSChange">
          <checkbox label="iOS"></checkbox>
          <checkbox label="Android"></checkbox>
          <checkbox label="Windows"></checkbox>
        </checkbox-group>
        <p>{{ os | json }}</p>
      </div>
      <div slot="desc">
        <p> <code>&lt;checkbox-group&gt;</code>  <code>&lt;checkbox&gt;</code> </p>
      </div>
      <x-code lang="html" slot="code">{{ code.group }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <checkbox-group v-model="os" @change="onOSChange" vertical>
          <checkbox label="iOS"></checkbox>
          <checkbox label="Android"></checkbox>
          <checkbox label="Windows"></checkbox>
        </checkbox-group>
        <p>{{ os | json }}</p>
      </div>
      <div slot="desc">
        <p> <code>vertical</code> </p>
      </div>
      <x-code lang="html" slot="code">{{ code.vertical }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <div class="mb10">
          <checkbox
            :value="checkAll"
            :indeterminate="indeterminate"
            @click.prevent.native="onCheckAllClick">
          </checkbox>
        </div>
        <checkbox-group v-model="os" @change="onOSChange">
          <checkbox label="iOS"></checkbox>
          <checkbox label="Android"></checkbox>
          <checkbox label="Windows"></checkbox>
        </checkbox-group>
        <p>{{ os | json }}</p>
      </div>
      <div slot="desc">
        <p> <code>indeterminate</code> </p>
      </div>
      <x-code lang="html" slot="code">{{ code.indeterminate }}</x-code>
      <x-code lang="html" slot="code">{{ code.indeterminateScript }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <checkbox-group v-model="foods">
          <checkbox label=""></checkbox>
          <checkbox label=""></checkbox>
          <checkbox label="" disabled></checkbox>
          <checkbox label="" disabled></checkbox>
        </checkbox-group>
        <p>{{ foods | json }}</p>
      </div>
      <div slot="desc">
        <p> <code>disabled</code> </p>
      </div>
      <x-code lang="html" slot="code">{{ code.disabled }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/checkbox'

export default {
  name: 'CheckboxDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      single: false,
      os: ['iOS'],
      foods: [''],
      indeterminate: true,
      checkAll: false
    }
  },

  methods: {
    onCheckAllClick () {
      if (this.indeterminate) {
        this.checkAll = false
      } else {
        this.checkAll = !this.checkAll
      }
      this.indeterminate = false

      if (this.checkAll) {
        this.os = ['iOS', 'Android', 'Windows']
      } else {
        this.os = []
      }
    },

    onOSChange (data) {
      if (data.length === 3) {
        this.indeterminate = false
        this.checkAll = true
      } else if (data.length > 0) {
        this.indeterminate = true
        this.checkAll = false
      } else {
        this.indeterminate = false
        this.checkAll = false
      }
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



