<template>
  <demo-article>
    <p></p>
    <example title="">
      <div slot="demo">
        <p></p>
        <x-button @click.native="open(false)"></x-button>
        <p></p>
        <x-button @click.native="open(true)"></x-button>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <p></p>
        <x-button @click.native="showInfo(false)"></x-button>
        <x-button @click.native="showSuccess(false)"></x-button>
        <x-button @click.native="showWarning(false)"></x-button>
        <x-button @click.native="showError(false)"></x-button>
        <p></p>
        <x-button @click.native="showInfo(true)"></x-button>
        <x-button @click.native="showSuccess(true)"></x-button>
        <x-button @click.native="showWarning(true)"></x-button>
        <x-button @click.native="showError(true)"></x-button>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.icon }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <x-button @click.native="showManual"></x-button>
      </div>
      <div slot="desc">
        <p><code>duration</code>0</p>
      </div>
      <x-code lang="html" slot="code">{{ code.manual }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/notice'

export default {
  name: 'NoticeDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  },

  methods: {
    open (withDesc) {
      this.$Notice.open({
        title: '',
        desc: withDesc ? '""' : ''
      })
    },

    showInfo (withDesc) {
      this.$Notice.info({
        title: '',
        desc: withDesc ? '""' : ''
      })
    },

    showSuccess (withDesc) {
      this.$Notice.success({
        title: '',
        desc: withDesc ? '""' : ''
      })
    },

    showWarning (withDesc) {
      this.$Notice.warning({
        title: '',
        desc: withDesc ? '""' : ''
      })
    },

    showError (withDesc) {
      this.$Notice.error({
        title: '',
        desc: withDesc ? '""' : ''
      })
    },

    showManual () {
      this.$Notice.error({
        title: '',
        desc: '',
        duration: 0
      })
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



