<template>
  <demo-article>
    <p></p>
    <example title="">
      <div slot="demo">
        <div class="demo-tooltip">
          <div class="top">
            <tooltip content="" placement="top-start">
              <x-button></x-button>
            </tooltip>
            <tooltip content="" placement="top">
              <x-button></x-button>
            </tooltip>
            <tooltip content="" placement="top-end">
              <x-button></x-button>
            </tooltip>
          </div>
          <div class="left">
            <tooltip content="" placement="left-start" class="mb5">
              <x-button></x-button>
            </tooltip>
            <tooltip content="" placement="left" class="mb5">
              <x-button></x-button>
            </tooltip>
            <tooltip content="" placement="left-end">
              <x-button></x-button>
            </tooltip>
          </div>
          <div class="right">
            <tooltip content="" placement="right-start" class="mb5">
              <x-button></x-button>
            </tooltip>
            <tooltip content="" placement="right" class="mb5">
              <x-button></x-button>
            </tooltip>
            <tooltip content="" placement="right-end">
              <x-button></x-button>
            </tooltip>
          </div>
          <div class="bottom">
            <tooltip content="" placement="bottom-start">
              <x-button></x-button>
            </tooltip>
            <tooltip content="" placement="bottom">
              <x-button></x-button>
            </tooltip>
            <tooltip content="" placement="bottom-end">
              <x-button></x-button>
            </tooltip>
          </div>
        </div>
      </div>
      <div slot="desc">
        <p> <code>top-start</code><code>top</code><code>top-end</code><code>left-start</code><code>left</code><code>left-end</code><code>bottom-start</code><code>bottom</code><code>bottom-end</code><code>right-start</code><code>right</code><code>right-end</code> 12</p>
      </div>
      <x-code lang="html" slot="code">{{ code.placement }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <tooltip content="" :delay="1000">
          <x-button>1</x-button>
        </tooltip>
      </div>
      <div slot="desc">
        <p> <code>delay</code> </p>
      </div>
      <x-code lang="html" slot="code">{{ code.delay }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/tooltip'

export default {
  name: 'TooltipDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      visible: false
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'

.demo-tooltip
  max-width 400px

  .top
    text-align center

  .left
    float left
    width 60px

  .right
    float right
    width 60px

  .bottom
    text-align center
    clear both
</style>



