let code = {}

code.base = `
<alert></alert>
<alert type="success"></alert>
<alert type="warning"></alert>
<alert type="error"></alert>
`

code.desc = `
<alert>
  
  <template slot="desc"></template>
</alert>
<alert type="success">
  
  <template slot="desc"></template>
</alert>
<alert type="warning">
  
  <template slot="desc"></template>
</alert>
<alert type="error">
  
  <template slot="desc"></template>
</alert>
`

code.icon = `
<p></p>
<alert show-icon></alert>
<alert type="success" show-icon></alert>
<alert type="warning" show-icon></alert>
<alert type="error" show-icon></alert>
<p></p>
<alert show-icon>
  
  <template slot="desc"></template>
</alert>
<alert type="success" show-icon>
  
  <template slot="desc"></template>
</alert>
<alert type="warning" show-icon>
  
  <template slot="desc"></template>
</alert>
<alert type="error" show-icon>
  
  <template slot="desc"></template>
</alert>
`

code.closable = `
<alert closable></alert>
<alert show-icon closable>
  
  <template slot="desc"></template>
</alert>
<alert type="warning" closable>
  
  <template slot="close"><span class="hl-info"></span></template>
</alert>
`

code.naked = `
<alert show-icon naked>
  
  <template slot="desc"></template>
</alert>
`

export default code



