<template>
  <demo-article>
    <p></p>
    <example title="">
      <div slot="demo">
        <tabs :nav="nav">
          <router-link :to="item.link" v-for="item in nav" tag="li" :key="item">{{ item.label }}</router-link>
        </tabs>
      </div>
      <div slot="desc">
        <p></p>
      </div>
      <div slot="code">
        <p></p>
      </div>
    </example>
    <example title="">
      <div slot="demo">
        <tabs :nav="nav" type="line">
          <router-link :to="item.link" v-for="item in nav" tag="li" :key="item">{{ item.label }}</router-link>
        </tabs>
      </div>
      <div slot="desc">
        <p> <code>type</code>  <code>card</code>  <code>line</code> <code>card</code></p>
      </div>
      <div slot="code">
        <p></p>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'

export default {
  name: 'TabsDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      nav: [{
        link: '/component/tabs',
        label: ''
      }, {
        link: 'users',
        label: ''
      }, {
        link: 'history',
        label: ''
      }]
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



