<template>
  <demo-article>
    <p></p>
    <example title="">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <dropdown>
              <x-button> <icon type="angle-down"></icon></x-button>
              <dropdown-menu slot="content">
                <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
              </dropdown-menu>
            </dropdown>
          </x-col>
          <x-col :span="12">
            <dropdown trigger="custom" :visible="visible">
              <x-button @click.native="visible=true"> <icon type="angle-down"></icon></x-button>
              <div slot="content" class="tac" style="width: 200px; height: 80px;">
                <p></p>
                <x-button type="primary" size="small" @click.native="visible=false"></x-button>
              </div>
            </dropdown>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p></p>
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
      <div slot="code">
        <p></p>
      </div>
    </example>
    <example title="">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <dropdown>
              <x-button> <icon type="angle-down"></icon></x-button>
              <dropdown-menu slot="content">
                <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
              </dropdown-menu>
            </dropdown>
          </x-col>
          <x-col :span="12" trigger="click">
            <dropdown trigger="click">
              <x-button> <icon type="angle-down"></icon></x-button>
              <dropdown-menu slot="content">
                <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
              </dropdown-menu>
            </dropdown>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p></p>
      </div>
      <x-code lang="html" slot="code">{{ code.trigger }}</x-code>
      <div slot="code">
        <p></p>
      </div>
    </example>
    <example title="">
      <div slot="demo">
        <x-row>
          <x-col :span="8">
            <dropdown placement="bottom-start">
              <x-button> <icon type="angle-down"></icon></x-button>
              <dropdown-menu slot="content">
                <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
              </dropdown-menu>
            </dropdown>
          </x-col>
          <x-col :span="8">
            <dropdown>
              <x-button> <icon type="angle-down"></icon></x-button>
              <dropdown-menu slot="content">
                <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
              </dropdown-menu>
            </dropdown>
          </x-col>
          <x-col :span="8">
            <dropdown placement="bottom-end">
              <x-button> <icon type="angle-down"></icon></x-button>
              <dropdown-menu slot="content">
                <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
              </dropdown-menu>
            </dropdown>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p></p>
      </div>
      <x-code lang="html" slot="code">{{ code.placement }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <dropdown @item-click="onItemClick">
          <x-button> <icon type="angle-down"></icon></x-button>
          <dropdown-menu slot="content">
            <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
          </dropdown-menu>
        </dropdown>
      </div>
      <div slot="desc">
        <p></p>
      </div>
      <x-code lang="html" slot="code">{{ code.item }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/dropdown'
import find from 'lodash/find'

export default {
  name: 'DropdownDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      visible: false,
      menu: [{
        name: 'link',
        label: ''
      }, {
        name: 'dev',
        label: ''
      }, {
        name: 'operation',
        label: ''
      }, {
        name: 'data',
        label: ''
      }]
    }
  },

  methods: {
    /**
     * 
     * @param  {String} name 
     */
    onItemClick (name) {
      let label = find(this.menu, { name }).label
      this.$Message.info(`${label}`)
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



