let code = {}

code.base = `
<template>
  <x-button @click.native="$Message.info('')"></x-button>
</template>
`

code.type = `
<template>
  <x-button @click.native="$Message.success('')"></x-button>
  <x-button @click.native="$Message.warning('')"></x-button>
  <x-button @click.native="$Message.error('')"></x-button>
</template>
`

code.delay = `
<template>
  <x-button @click.native="$Message.info('5', 5)">5</x-button>
</template>
`
code.callback = `
<template>
  <x-button @click.native="$Message.info('3', 3, showSuccess)"></x-button>
</template>
<script>
export default {
  methods: {
    showSuccess () {
      this.$Message.success('')
    }
  }
}
</script>
`

export default code



