<template>
  <demo-article>
    <p></p>
    <example title="">
      <div slot="demo">
        <x-select v-model="os" style="width: 200px;">
          <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
        </x-select>
      </div>
      <div slot="desc">
        <p> <code>v-model</code> </p>
        <p><code>value</code>  <code>&lt;x-option&gt;</code>  <code>value</code></p>
        <p> <code>&lt;x-select&gt;</code> <code>style</code> </p>
        <p> <code>up</code>  <code>down</code> EnterEsc</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.base }}</x-code>
        <x-code lang="js" slot="code">{{ code.baseData }}</x-code>
      </div>
    </example>
    <example title="">
      <div slot="demo">
        <x-row>
          <x-col :span="8">
            <x-select v-model="os" size="small">
              <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
            </x-select>
          </x-col>
          <x-col :span="8">
            <x-select v-model="os">
              <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
            </x-select>
          </x-col>
          <x-col :span="8">
            <x-select v-model="os" size="large">
              <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
            </x-select>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p> <code>size</code>  <code>small</code><code>large</code></p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.size }}</x-code>
      </div>
    </example>
    <example title="">
      <div slot="demo">
        <div>
          <x-select v-model="os" style="width: 200px;" disabled>
            <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
          </x-select>
        </div>
        <div>
          <x-select v-model="os" style="width: 200px;">
            <x-option v-for="item in osList" :value="item.value" :key="item" :disabled="item.disabled">{{ item.label }}</x-option>
          </x-select>
        </div>
      </div>
      <div slot="desc">
        <p>sasd</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.disabled }}</x-code>
      </div>
    </example>
    <example title="">
      <div slot="demo">
        <x-select v-model="os" style="width: 200px;" clearable>
          <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
        </x-select>
      </div>
      <div slot="desc">
        <p></p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.clearable }}</x-code>
      </div>
    </example>
    <example title="">
      <div slot="demo">
        <div class="mb10">{{ osArr | json}}</div>
        <x-select v-model="osArr" style="width: 200px;" multiple>
          <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
        </x-select>
      </div>
      <div slot="desc">
        <p></p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.multiple }}</x-code>
        <x-code lang="js" slot="code">{{ code.multipleData }}</x-code>
      </div>
    </example>
    <example title="">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <x-select v-model="os" style="max-width: 200px;" filterable>
              <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
            </x-select>
          </x-col>
          <x-col :span="12">
            <x-select v-model="osArr" style="max-width: 200px;" multiple filterable>
              <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
            </x-select>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p></p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.filterable }}</x-code>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/select'

export default {
  name: 'SelectDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      os: '',
      osArr: [],
      osList: [{
        value: 'windows',
        label: 'Windows',
        disabled: false
      }, {
        value: 'android',
        label: 'Android',
        disabled: true
      }, {
        value: 'linux',
        label: 'Linux',
        disabled: false
      }, {
        value: 'mac',
        label: 'Mac OS',
        disabled: false
      }]
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



