<template>
  <demo-article>
    <p>-</p>
    <example title="">
      <div slot="demo">
        <radio v-model="single"></radio>
        <p>{{ single }}</p>
      </div>
      <div slot="desc">
        <p><code>v-model</code></p>
      </div>
      <x-code lang="html" slot="code">{{ code.single }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <radio-group v-model="os">
          <radio label="iOS"></radio>
          <radio label="Android"></radio>
          <radio label="Windows"></radio>
        </radio-group>
        <p>{{ os }}</p>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.group }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <radio-group v-model="os" vertical>
          <radio label="iOS"></radio>
          <radio label="Android"></radio>
          <radio label="Windows"></radio>
        </radio-group>
        <p>{{ os }}</p>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.vertical }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <radio-group type="button" v-model="period">
          <radio label=""></radio>
          <radio label=""></radio>
          <radio label=""></radio>
        </radio-group>
        <p>{{ period }}</p>
      </div>
      <div slot="desc">
        <p><code>type</code>'button'</p>
      </div>
      <x-code lang="html" slot="code">{{ code.button }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <div class="mb10">
          <radio-group type="button" size="small" v-model="period">
            <radio label=""></radio>
            <radio label=""></radio>
            <radio label=""></radio>
          </radio-group>
        </div>
        <div class="mb10">
          <radio-group type="button" v-model="period">
            <radio label=""></radio>
            <radio label=""></radio>
            <radio label=""></radio>
          </radio-group>
        </div>
        <div class="mb10">
          <radio-group type="button" size="large" v-model="period">
            <radio label=""></radio>
            <radio label=""></radio>
            <radio label=""></radio>
          </radio-group>
        </div>
        <p>{{ period }}</p>
      </div>
      <div slot="desc">
        <p><code>size</code> <code>small</code>  <code>large</code></p>
      </div>
      <x-code lang="html" slot="code">{{ code.size.small }}</x-code>
      <x-code lang="html" slot="code">{{ code.size.normal }}</x-code>
      <x-code lang="html" slot="code">{{ code.size.large }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <radio-group type="button" v-model="period">
          <radio label="" disabled></radio>
          <radio label="" disabled></radio>
          <radio label="" disabled></radio>
        </radio-group>
        <radio v-model="disabledSingle" disabled></radio>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.disabled }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/radio'

export default {
  name: 'RadioDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      single: false,
      disabledSingle: true,
      os: 'iOS',
      period: ''
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



