<template>
  <demo-article>
    <p> <code>window.confirm</code></p>
    <example title="">
      <div slot="demo">
        <p v-for="type in confirmTypes">
          <x-button @click.native="showConfitm(type)">confirm({{type}})</x-button>
        </p>
      </div>
      <div slot="desc">
        <p><code>$Confirm</code> <code>promise</code> <code>async</code> </p>
      </div>
      <div slot="code">
        <x-code lang="javascript" slot="code" v-for="type in ['info', 'warning', 'error', 'success', 'base', 'promise', 'async']" :key="type">{{ code[type] }}</x-code>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/confirm'

export default {
  name: 'confirmDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      confirmTypes: ['info', 'warning', 'error', 'success']
    }
  },

  methods: {
    async showConfitm (type) {
      if (await this.$Confirm[type]({
        title: '',
        content: ', , '
      })) {
        console.log('')
      } else {
        console.log('')
      }
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



