let code = {}

code.base = `
// 
<cascader style="width: 200px;" :data="spots" :value="spot" @change="onCascaderChange"></cascader>
`

code.baseData = `
// 
data () {
  return {
    spot: [],
    spotSync: [{
      value: 'beijing',
      label: ''
    }, {
      value: 'tiantan',
      label: ''
    }],
    spots: [{
      value: 'beijing',
      label: '',
      children: [{
        value: 'gugong',
        label: ''
      }, {
        value: 'tiantan',
        label: ''
      }, {
        value: 'wangfujing',
        label: ''
      }]
    }, {
      value: 'jiangsu',
      label: '',
      children: [{
        value: 'nanjing',
        label: '',
        children: [{
          value: 'fuzimiao',
          label: ''
        }]
      }, {
        value: 'suzhou',
        label: '',
        children: [{
          value: 'zhuozhengyuan',
          label: ''
        }, {
          value: 'shizilin',
          label: ''
        }]
      }]
    }]
  }
}

methods: {
  onCascaderChange (value) {
    console.log(value)
    this.spot = value
  }
}
`

code.sync = `
// 
<cascader style="width: 200px;" :data="spots" v-model="spotSync"></cascader>
`

code.syncData = `
// 
data () {
  return {
    spotSync: []
  }
}
`

code.small = `
<cascader style="width: 200px;" :data="spots" size="small" v-model="spotSmall"></cascader>
`
code.normal = `
<cascader style="width: 200px;" :data="spots" v-model="spotNormal"></cascader>
`
code.large = `
<cascader style="width: 200px;" :data="spots" size="large" v-model="spotLarge"></cascader>
`
export default code



