<template>
  <demo-article>
    <p></p>
    <example title="">
      <div slot="demo">
        <alert></alert>
        <alert type="success"></alert>
        <alert type="warning"></alert>
        <alert type="error"></alert>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <alert>
          
          <template slot="desc"></template>
        </alert>
        <alert type="success">
          
          <template slot="desc"></template>
        </alert>
        <alert type="warning">
          
          <template slot="desc"></template>
        </alert>
        <alert type="error">
          
          <template slot="desc"></template>
        </alert>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.desc }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <p></p>
        <alert show-icon></alert>
        <alert type="success" show-icon></alert>
        <alert type="warning" show-icon></alert>
        <alert type="error" show-icon></alert>
        <p></p>
        <alert show-icon>
          
          <template slot="desc"></template>
        </alert>
        <alert type="success" show-icon>
          
          <template slot="desc"></template>
        </alert>
        <alert type="warning" show-icon>
          
          <template slot="desc"></template>
        </alert>
        <alert type="error" show-icon>
          
          <template slot="desc"></template>
        </alert>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.icon }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <p></p>
        <alert show-icon size="large">
          <template slot="desc"></template>
        </alert>
        <p></p>
        <alert show-icon size="large">
          
          <template slot="desc"></template>
        </alert>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.icon }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <alert closable></alert>
        <alert show-icon closable>
          
          <template slot="desc"></template>
        </alert>
        <alert type="warning" closable>
          
          <template slot="close"><span class="hl-info"></span></template>
        </alert>
      </div>
      <div slot="desc">
        <p> <code>closable</code> </p>
      </div>
      <x-code lang="html" slot="code">{{ code.closable }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <alert show-icon naked>
          
          <template slot="desc"></template>
        </alert>
      </div>
      <div slot="desc">
        <p> <code>naked</code> </p>
      </div>
      <x-code lang="html" slot="code">{{ code.naked }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/alert'

export default {
  name: 'AlertDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



