<template>
  <demo-article>
    <p></p>
    <example title="">
      <div slot="demo">
        <scroll-view class="scroll-view" >
          <div class="scroll-content"></div>
        </scroll-view>
      </div>
      <div slot="desc">
        <p>, , </p>
        <p>, , </p>
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
      <x-code lang="html" slot="code">{{ code.details }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/scroll-view'

export default {
  name: 'ScrollBarDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'

.scroll-view
  width 100%
  height 200px
  .scroll-content
    width 1000px
    height 400px
    background linear-gradient(0, red, rgba(0, 0, 255, 0.5))
</style>



