let code = {}

code.base = `
<template>
  <p></p>
  <x-button @click.native="open(false)"></x-button>
  <p></p>
  <x-button @click.native="open(true)"></x-button>
</template>
<script>
  export default {
    methods: {
      open (withDesc) {
        this.$Notice.open({
          title: '',
          desc: withDesc ? '""' : ''
        })
      }
    }
  }
<script>
`

code.icon = `
<template>
  <p></p>
  <x-button @click.native="showInfo(false)"></x-button>
  <x-button @click.native="showSuccess(false)"></x-button>
  <x-button @click.native="showWarning(false)"></x-button>
  <x-button @click.native="showError(false)"></x-button>
  <p></p>
  <x-button @click.native="showInfo(true)"></x-button>
  <x-button @click.native="showSuccess(true)"></x-button>
  <x-button @click.native="showWarning(true)"></x-button>
  <x-button @click.native="showError(true)"></x-button>
</template>
<script>
  export default {
    methods: {
      showInfo (withDesc) {
        this.$Notice.info({
          title: '',
          desc: withDesc ? '""' : ''
        })
      },

      showSuccess (withDesc) {
        this.$Notice.success({
          title: '',
          desc: withDesc ? '""' : ''
        })
      },

      showWarning (withDesc) {
        this.$Notice.warning({
          title: '',
          desc: withDesc ? '""' : ''
        })
      },

      showError (withDesc) {
        this.$Notice.error({
          title: '',
          desc: withDesc ? '""' : ''
        })
      }
    }
  }
</script>
`

code.manual = `
<template>
  <x-button @click.native="showManual"></x-button>
</template>
<script>
  export default {
    methods: {
      showManual () {
        this.$Notice.error({
          title: '',
          desc: '',
          duration: 0
        })
      }
    }
  }
</script>
`

export default code



