<template>
  <demo-article>
    <p></p>
    <example title="">
      <div slot="demo">
        <x-switch v-model="s1" @change="$Message.info('' + s1)"></x-switch>
      </div>
      <div slot="desc">
        <p> <code>v-mode</code> </p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.base }}</x-code>
      </div>
    </example>
    <example title="">
      <div slot="demo">
        <div class="mb10">
          <x-switch size="small"></x-switch>
           App Store 
        </div>
        <div class="mb10">
          <x-switch :width="60"><span slot="on">ON</span><span slot="off">OFF</span></x-switch>
        </div>
      </div>
      <div slot="desc">
        <p> <code>size</code> </p>
        <p> <code>on</code>  <code>off</code>  <code>width</code> </p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.size }}</x-code>
        <x-code lang="html" slot="code">{{ code.width }}</x-code>
      </div>
    </example>
    <example title="">
      <div slot="demo">
        <x-switch type="success" :value="true"></x-switch>
      </div>
      <div slot="desc">
        <p> <code>type</code>  <code>primary</code>  <code>success</code></p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.type }}</x-code>
      </div>
    </example>
    <example title="">
      <div slot="demo">
        <x-switch disabled></x-switch>
        <x-switch disabled :value="true"></x-switch>
        <x-switch type="success" disabled :value="true"></x-switch>
      </div>
      <div slot="desc">
        <p> <code>disabled</code> </p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.disabled }}</x-code>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/switch'

export default {
  name: 'SwitchDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      s1: false
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



