<template>
  <demo-article>
    <p>, </p>
    <example title="">
      <div slot="demo">
        <validator name="validatorName">
          <span></span>
          <input class="mb30" type="text" placeholder="2" name="test"  v-model="val" :rules="{maxlength: 2}">
          <div class="aaaaa">
            <span></span>
            <x-input
              class="mb30"
              placeholder=""
              v-model="val1"
              name="name"
              :rules="{ required: {value: true, message: ''}, phone: true }"
              delay="1000"
              >
            </x-input>
            <span></span>
            <x-input
              class="mb30"
              placeholder=""
              v-model.lazy="val2"
              name="password"
              :rules="{required: true}"
              >
            </x-input>
          </div>
        </validator>
        <x-button @click.native="checkItem('password')"></x-button>
        <x-button @click.native="checkAll"></x-button>
        <x-button @click.native="reset"></x-button>
        <x-button @click.native="logValidator"></x-button>
      </div>
      <div slot="desc">
        <p>, name, rules, rules, div, valuename</p>
        <p>rules, : {require: true, max: 1000}, </p>
        <p>, rules, : {max: {value: 1000, message: '{value}'}}, {value}rules</p>
        <p>, , validator:use-default-stylefalse</p>
        <p>, 500(), validator:delay, </p>
        <p>, validatordelay, </p>
        <p>, , 100000</p>
        <p>, </p>
        <br>
        <p>:</p>
        <pre>
          // : (rules)
          {
            required:  (Boolean),
            max:  (Number),
            min:  (Number),
            maxlength:  (Number),
            minlength:  (Number),
            number:  (Boolean),
            integer:  (Boolean),
            phone:  (Boolean),
            email:  (Boolean),
            url: url (Boolean),
            regexp:  (RegExp),
            method:  (Function)
          }
          // (this.$Validator.XXX)
          {
            formItems: [
              value: value
              name: name
              rules: 
              delay: 
              messages: 
              elm: DOM
              vNode: 
              isComponent: 
              dirty:  (, )
              results: 
              errors: 
              isRight: 
              isError: 
              isShowError: 
            ]
            isError:  (, )
            isShowError:  (, )
            reset:  ()
            validate:  
          }
        </pre>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.base }}</x-code>
        <x-code lang="javascript" slot="code">{{ code.js }}</x-code>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/validator'

export default {
  name: 'ValidatorDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      val: '',
      val1: '',
      val2: ''
    }
  },

  methods: {
    /**
     * 
     */
    checkItem (formItemName) {
      // 
      // 
      this.$Validator['validatorName'].validate(formItemName)
    },
    /**
     * 
     */
    checkAll () {
      // 
      // 
      this.$Validator['validatorName'].validate()
    },

    /**
     * 
     */
    reset () {
      this.$Validator['validatorName'].reset()
    },

    /**
     * 
     */
    logValidator () {
      console.log(this.$Validator['validatorName'])
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



