<template>
  <demo-article>
    <p></p>
    <example title="">
      <div slot="demo">
        <x-button @click.native="$Message.info('')"></x-button>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <x-button @click.native="$Message.success('')"></x-button>
        <x-button @click.native="$Message.warning('')"></x-button>
        <x-button @click.native="$Message.error('')"></x-button>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.type }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <x-button @click.native="$Message.info('5', 5)">5</x-button>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.delay }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <p>3</p>
        <x-button @click.native="$Message.info('3', 3, showSuccess)"></x-button>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.callback }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/message'

export default {
  name: 'MessageDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  },

  methods: {
    showSuccess () {
      this.$Message.success('')
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



