let code = {}

code.info = `
// (info)
this.$Confirm.info(', ')
`
code.warning = `
// (warning)
this.$Confirm.warning(', ')
`
code.error = `
// (error)
this.$Confirm.error(', ')
`
code.success = `
// (success)
this.$Confirm.success(', ')
`

code.base = `
// 
this.$Confirm.show({
  title: '',
  content: '',
  type: 'warning'
})
`

code.promise = `
// promise
this.$Confirm.warning('').then((val) => {
  val ? console.log('') : console.log('')
}).catch((err) => {
  console.log(err)
})
`

code.async = `
// async
async showConfitm () {
  if (await this.$Confirm.warning('')) {
    console.log('')
  } else {
    console.log('')
  }
}
`

export default code



