<template>
  <demo-article>
    <p></p>
    <example title="">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <date-picker
              style="width: 200px"
              type="date"
              placeholder="">
            </date-picker>
          </x-col>
          <x-col :span="12">
            <date-picker
              style="width: 200px"
              type="daterange"
              placeholder="">
            </date-picker>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p> <code>type</code>  date>  daterange </p>
        <p>  <code>placement</code>  12 </p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.base" :key="item">{{ item }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <date-picker
              style="width: 200px"
              type="date"
              placeholder=""
              :options="dateOptions">
            </date-picker>
          </x-col>
          <x-col :span="12">
            <date-picker
              style="width: 200px"
              type="daterange"
              placeholder=""
              :options="daterangeOptions"
              @on-change="onDateRangeChange">
            </date-picker>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p> <code>options</code>  <code>shortcuts</code> </p>
        <p>  <code>value</code>  <code>range</code> <code>value</code>  2016-12-24  12/24/16 </p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.shortcut" :key="item">{{ item }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <div class="mb10">
          <date-picker
            style="width: 200px;"
            type="date"
            placeholder=""
            format="yyyyMMdd"
            :value="date1">
          </date-picker>
        </div>
        <div class="mb10">
          <date-picker
            style="width: 240px;"
            type="daterange"
            format="yyyy/MM/dd"
            placeholder=""
            :value="daterange1">
          </date-picker>
        </div>
      </div>
      <div slot="desc">
        <p> <code>format</code> </p>
        <p><code>format</code>  <code>value</code> </p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.format" :key="item">{{ item }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <div class="mb10">
          <date-picker
            style="width: 200px;"
            type="datetime"
            placeholder="">
          </date-picker>
        </div>
        <div class="mb10">
          <date-picker
            style="width: 200px;"
            type="datetime"
            format="yyyy-MM-dd HH:mm"
            placeholder="()">
          </date-picker>
        </div>
        <div class="mb10">
          <date-picker
            style="width: 300px;"
            type="datetimerange"
            placeholder="">
          </date-picker>
        </div>
        <div class="mb10">
          <date-picker
            style="width: 300px;"
            type="datetimerange"
            format="yyyy-MM-dd HH:mm"
            placeholder="()">
          </date-picker>
        </div>
      </div>
      <div slot="desc">
        <p> <code>type</code>  datetime  datetimerange </p>
        <p> <code>format</code></p>
      </div>
      <x-code lang="html" slot="code">{{ code.datetime }}</x-code>
      <x-code lang="html" slot="code">{{ code.datetimerange }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <date-picker
              style="width: 200px;"
              type="year"
              placeholder="">
            </date-picker>
          </x-col>
          <x-col :span="12">
            <date-picker
              style="width: 200px;"
              type="month"
              placeholder="">
            </date-picker>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p> <code>type</code>  year  month </p>
      </div>
      <x-code lang="html" slot="code">{{ code.year }}</x-code>
      <x-code lang="html" slot="code">{{ code.month }}</x-code>
    </example>
    <example title="">
      <div slot="demo">
        <div class="mb10">
          <date-picker
            style="max-width: 200px"
            type="date"
            size="small"
            placeholder="">
          </date-picker>
        </div>
        <div class="mb10">
          <date-picker
            style="max-width: 200px"
            type="date"
            placeholder="">
          </date-picker>
        </div>
        <div class="mb10">
          <date-picker
            style="max-width: 200px"
            type="date"
            size="large"
            placeholder="">
          </date-picker>
        </div>
      </div>
      <div slot="desc">
        <p> <code>size</code> </p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.size" :key="item">{{ item }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/date-picker'

export default {
  name: 'DatePickerDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      date1: '2016-01-01',
      daterange1: ['2016-01-01', '2016-02-15'],
      dateOptions: {
        shortcuts: [{
          text: '',
          value () {
            return new Date()
          },
          onClick: () => {
            this.$Message.info('')
          }
        }, {
          text: '',
          value () {
            let date = new Date()
            date.setTime(date.getTime() - 24 * 3600 * 1000)
            return date
          },
          onClick: () => {
            this.$Message.info('')
          }
        }, {
          text: '',
          value () {
            let date = new Date()
            date.setTime(date.getTime() - 7 * 24 * 3600 * 1000)
            return date
          },
          onClick: () => {
            this.$Message.info('')
          }
        }]
      },
      daterangeOptions: {
        shortcuts: [{
          text: '',
          value () {
            let end = new Date()
            let start = new Date()
            start.setTime(start.getTime() - 7 * 24 * 3600 * 1000)
            return [start, end]
          },
          onClick: () => {
            this.$Message.info('')
          }
        }, {
          text: '',
          value () {
            let end = new Date()
            let start = new Date()
            start.setTime(start.getTime() - 30 * 24 * 3600 * 1000)
            return [start, end]
          },
          onClick: () => {
            this.$Message.info('')
          }
        }, {
          text: '',
          value () {
            let end = new Date()
            let start = new Date()
            start.setTime(start.getTime() - 90 * 24 * 3600 * 1000)
            return [start, end]
          },
          onClick: () => {
            this.$Message.info('')
          }
        }]
      }
    }
  },

  methods: {
    onDateRangeChange (range) {
      this.$Message.info(`${range[0]}${range[1]}`)
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



